import React from 'react';
import NavImage from '../media/Aman.jpeg';
import { NavLink } from 'react-router-dom';

const Navigation = () => {
    return (
        <div className="sidebar">
            <div className="id">
                <div className="idContent">
                    <img src={NavImage} alt="profile-pic" />
                    <h3>Aman Yadav</h3>
                </div>
            </div>

            <div className="navigation">
                <ul>
                    <li>
                    <NavLink exact to="/" activeClassName="navAction">
                        <i className="fas fa-home"></i>
                        <span>Accueil</span>
                    </NavLink>
                    </li>
                    <li>
                    <NavLink exact to="/knowledges" activeClassName="navAction">
                        <i className="fas fa-mountain"></i>
                        <span>Knowledges</span>
                    </NavLink>
                    </li>
                    <li>
                    <NavLink exact to="/contact" activeClassName="navAction">
                        <i className="fas fa-address-book"></i>
                        <span>Contact</span>
                    </NavLink>
                    </li>
                </ul>
            </div>

            <div className="socialNetwork">
                <ul>
                    <li>
                        <a href="https://www.google.com" target="_blank"
                        rel="noopener noreferrer" >
                        <i className="fab fa-linkedin"></i>
                        </a>
                    </li>
                    <li>
                        <a href="https://www.google.com" target="_blank"
                        rel="noopener noreferrer" >
                        <i className="fab fa-github"></i>
                        </a>
                    </li>
                    <li>
                        <a href="https://www.google.com" target="_blank"
                        rel="noopener noreferrer" >
                        <i className="fab fa-twitter"></i>
                        </a>
                    </li>
                    <li>
                        <a href="https://www.google.com" target="_blank"
                        rel="noopener noreferrer" >
                        <i className="fab fa-youtube"></i>
                        </a>
                    </li>
                </ul>
                <div className="signature">
                    <p>fromAmn - 2021</p>
                </div>
            </div>

        </div>
    );
};

export default Navigation;